@import url(https://fonts.googleapis.com/css?family=Walter+Turncoat);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  font-family: 'Walter Turncoat', cursive;
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

body {
  background-color: #282c34;
}
.App-header {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  color: white;
  padding: 30pt;
}

.App-header h2 {
  font-size: calc(30px + 2vmin);
  font-weight: normal;
  text-align: left;
  -webkit-align-items: initial;
          align-items: initial;
  margin-top: 0;
  margin-bottom: 10px;
}  

hr.separator {
  margin: 30px 0px;
}

.input {
  background-color: unset;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  -webkit-text-fill-color: yellow !important;
  border-bottom: 1px dashed #fff;
  font-family: 'Walter Turncoat', cursive;
  text-align: center;
  font-size: larger;
  margin: auto;
  padding: 0px 10px 3px 10px;
  min-width: 60px;
}

.varsTable {
  width: 100%;
}

.varsTable th {
  font-weight: normal;
  font-size: larger;
}

.varsTable td {
  text-align: center; 
}

ul {
  list-style-type: none;
  margin: auto;
}

ul.endings {
  margin: 0;
  font-size: xx-large;
}

li {
  display: inline;
  margin-right: 0px;
  padding: 10px;
}

li::after {
  content: " / ";
  padding: 0 20px;
}

li:last-child::after {
  content: "";
  padding: 0;
}

li a {
  text-decoration: none;
  color: #fff;
  -webkit-transition: all 0.5s ease;
}

li a:hover, li a.selected {
  -webkit-text-decoration-line: underline;
          text-decoration-line: underline;
  -webkit-text-decoration-style: double;
          text-decoration-style: double;
  text-underline-offset: 3px;
}

li.active a {
  color: #fff;
}

a.randomGet {
  text-decoration: none;
  color: #fff;
  -webkit-transition: all 0.5s ease;
}

.exampleBox {
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  height: auto;
  margin-left: auto;
  margin-right: auto;
}

.exampleBox .val {
  font-size: 35pt;
  height: 60px;
}  

.exampleBox .label {
  font-size: 14pt;
}

.exampleBox .right {
  color: green;
}

.exampleBox td {
  padding: 0 15px;
}  

.copy-icon {
  border: solid 1px #fff;
  background-color: #ccc;
  display: block;
  border-radius: 10px;
  padding: 5px;
}
.copy-icon img {
  -webkit-filter: invert(97%) sepia(100%) saturate(0%) hue-rotate(348deg) brightness(102%) contrast(103%);
          filter: invert(97%) sepia(100%) saturate(0%) hue-rotate(348deg) brightness(102%) contrast(103%); 
}

.randomAll {
  font-size: x-large;
  padding: 10px;
  cursor: pointer;
}
